
import React from "react";
import Slider from "react-slick";

import "../../styles/testimonial.css";

import ava01 from "../../assets/all-images/ava-1.jpg";
import ava02 from "../../assets/all-images/ava-2.jpg";
import ava03 from "../../assets/all-images/ava-3.jpg";
import ava04 from "../../assets/all-images/ava-4.jpg";

const Testimonial = () => {
	const settings = {
		dots: true,
		infinite: true,
		autoplay: true,
		speed: 1000,
		swipeToSlide: true,
		autoplaySpeed: 2000,
		slidesToShow: 3,
		slidesToScroll: 1,
		responsive: [
			{
				breakpoint: 992,
				settings: {
					slidesToShow: 2,
					slidesToScroll: 1,
					infinite: true,
					dots: true,
				},
			},
			{
				breakpoint: 576,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
				},
			},
		],
	};

	return (
		<Slider {...settings}>
			<div className="testimonial py-4 px-3">
				<p className="section__description">
				I agree with the other reviews that this place is great. Given that ratings on Yelp for car rental companies tend to be skewed really low, 4 stars is pretty exceptional for any car rental place. That was more than enough to convince me to check this place out. I rented from them from for 3 days and it was fantastic. 
				</p>

				<div className="mt-3 d-flex align-items-center gap-4">
					<img src={ava01} alt="" className="w-25 h-25 rounded-2" />

					<div>
						<h6 className="mb-0 mt-3">Justin T.</h6>
						<p className="section__description">Customer</p>
					</div>
				</div>
			</div>

			<div className="testimonial py-4 px-3">
				<p className="section__description">
				I recommend everyone to go and rent cars from here before settling elsewhere.	
				Once I couldn't afford to pay the asked price for the car i wanted, she was able to find another vehicle with the same features but in my budget.
				</p>

				<div className="mt-3 d-flex align-items-center gap-4">
					<img src={ava02} alt="" className="w-25 h-25 rounded-2" />

					<div>
						<h6 className="mb-0 mt-3">Debbie G.</h6>
						<p className="section__description">Customer</p>
					</div>
				</div>
			</div>

			<div className="testimonial py-4 px-3">
				<p className="section__description">
				One key thing to note, unlike some of the other major car brands, they have a limited range that they will set for your travel, but as long as you're not going on a far away trip you should be fine.
				</p>

				<div className="mt-3 d-flex align-items-center gap-4">
					<img src={ava03} alt="" className="w-25 h-25 rounded-2" />

					<div>
						<h6 className="mb-0 mt-3">Kyle S.</h6>
						<p className="section__description">Customer</p>
					</div>
				</div>
			</div>

			<div className="testimonial py-4 px-3">
				<p className="section__description">
				A while back I was in a pinch and needed a rental car right away. I normally shy away from the "Non-Chain" agency's but this time I took a chance. What a great experience. I was greeted by Ali and ushered into a CLEAN office to do my paperwork, which took no time at all
				</p>

				<div className="mt-3 d-flex align-items-center gap-4">
					<img src={ava04} alt="" className="w-25 h-25 rounded-2" />

					<div>
						<h6 className="mb-0 mt-3">Yessy G.</h6>
						<p className="section__description">Customer</p>
					</div>
				</div>
			</div>
		</Slider>
	);
};

export default Testimonial;
